  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

:root {
  --header-height: 20vh;
  --ff-primary: Inter, sans-serif;
  --ff-secondary: Allura, cursive;
  --content-padding: calc(var(--header-height) + 3rem);
}

/* @font-face {
  font-family: Inter;
  src: url('../inter-variablefont_slntwght-webfont.woff2') format('woff2'), url('../inter-variablefont_slntwght-webfont.woff') format('woff');
  font-weight: 200;
} */

.inter-x {
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  font-weight: 900;
  font-style: normal;
  font-variation-settings:
    "slnt" 0;
}

body {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.9)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100svh;
  background-position: center;
  position: relative;
}

header {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  height: var(--header-height);
  position: fixed;
  width: 100%;
  /* flex-wrap: wrap; */

  h2 {
    font-size: 3rem;
    color: orange;
    font-family: var(--ff-secondary);
  }
}

nav {
  display: flex;
  gap: 2rem;
}

button {
  position: relative;
  background-color: transparent;
  color: #b4b4b4;
  border: none;
  border-radius: .2rem;
  /* padding: .8rem 2.8rem; */
  cursor: pointer;
  outline: 0px solid white;
  outline-offset: 0px;
  transition: all .1s cubic-bezier(0.165, 0.84, 0.44, 1);
  font-size: 1.2rem;
  font-family: var(--ff-primary);
  font-weight: 400;
}

button:hover {
  color: #fff;
}

 button.active::after{
  content: "";
  position: absolute;
  bottom: -.3rem;
  left: 0;
  width: 100%;
  height: 1.5px;
  background-color: orange;
  transition: all .4s cubic-bezier(0.075, 0.82, 0.165, 1); 
  backdrop-filter: blur(5px);
  border-radius: 1rem;
}

button.active, button:focus-visible {
  color: white;
}



.content {
  min-height: 100svh;
  display: flex;
  justify-content: center;
  align-items: center;

  .hero {
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 1rem;

    h1 {
      color: white;
      font-size: clamp(2.2rem, 4vw, 3.8rem);
      max-width: 30ch;
    }

    .dress {
      color: #b4b4b4;
      font-size: 1.5rem;
    }

    button {
      margin-top: 2rem;
      background-color: orange;
      align-self: center;
      color: black;
      border-radius: 100vh;
      padding: .8rem 2.5rem;
      font-family: var(--ff-primary);
      font-weight: 900;
    }
  }

  .contain {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
  }

  .menuList {
    align-self: baseline;
    padding-top: var(--content-padding);
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    grid-auto-rows: 1fr;
    gap: 2rem;
    list-style: none;

    li {
      border-radius: 1rem;
      padding: .5rem;
      backdrop-filter: blur(25px);
      box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);

      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    img{
      max-width: 100%;
      height: 200px;
      width: 100%;
      object-fit: cover;
      border-radius: .5rem;
    }

    .menuContent {
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: .5rem;
    }

    h4{
      font-family: var(--ff-primary);
      font-weight: 300;
      letter-spacing: .055rem;
      text-transform: capitalize;
      color: white;
    }

    span{
      font-family: var(--ff-primary);
      font-weight: 900;
      color: orange;
      font-size: 1.2rem;
    }
  }

  .about {
    padding-top: var(--content-padding);
    min-height: 100svh;
    color: white;
    font-size: 1.2rem;
    line-height: 1.7;
    font-family: var(--ff-primary);

    p {
      backdrop-filter: blur(10px);
    }
  }
}

